.qs_main {
  background-color: #dddddd;
  text-align: left;
  position: absolute;
  width: 200px;
  font: 12px sans-serif;
  box-shadow: 5px 5px 8px rgba(0, 0, 0, 0.35);
  user-select: none;
  -webkit-user-select: none;
  color: #000000;
  border: none;
}
.qs_content {
  background-color: #cccccc;
  overflow-y: auto;
}
.qs_title_bar {
  background-color: #eeeeee;
  user-select: none;
  -webkit-user-select: none;
  cursor: pointer;
  padding: 5px;
  font-weight: bold;
  border: none;
  color: #000000;
}
.qs_container {
  margin: 5px;
  padding: 5px;
  background-color: #eeeeee;
  border: none;
  position: relative;
}
.qs_container_selected {
  border: none;
  background-color: #ffffff;
}
/*========== range ==========*/
.qs_range {
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 100%;
  height: 17px;
  padding: 0px;
  margin: 0px;
  background-color: transparent;
  border: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.qs_range:focus {
  outline: none;
  border: none;
}
.qs_range::-webkit-slider-runnable-track {
  width: 100%;
  height: 15px;
  cursor: pointer;
  background: #cccccc;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
}
.qs_range:focus::-webkit-slider-runnable-track {
  background: #cccccc;
}
.qs_range::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 15px;
  width: 15px;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  background: #999999;
  cursor: pointer;
  margin-top: 0px;
}
.qs_range::-moz-range-track {
  width: 100%;
  height: 15px;
  cursor: pointer;
  background: #cccccc;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
}
.qs_range::-moz-range-thumb {
  height: 15px;
  width: 15px;
  border: none;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  background: #999999;
  cursor: pointer;
}
.qs_range::-ms-track {
  width: 100%;
  height: 15px;
  cursor: pointer;
  visibility: hidden;
  background: transparent;
}
.qs_range::-ms-thumb {
  height: 15px;
  width: 15px;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  background: #999999;
  cursor: pointer;
  border: none;
}
.qs_range::-ms-fill-lower {
  background: #cccccc;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
}
.qs_range:focus::-ms-fill-lower {
  background: #cccccc;
}
.qs_range::-ms-fill-upper {
  background: #cccccc;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
}
.qs_range:focus::-ms-fill-upper {
  background: #cccccc;
}
/*========== end range ==========*/
.qs_button {
  background-color: #f6f6f6;
  color: #000000;
  height: 30px;
  border: 1px solid #aaaaaa;
  font: 12px sans-serif;
}
.qs_button:active {
  background-color: #ffffff;
  border: 1px solid #aaaaaa;
}
.qs_button:focus {
  border: 1px solid #aaaaaa;
  outline: none;
}
.qs_checkbox {
  cursor: pointer;
  display: inline;
}
.qs_checkbox input {
  position: absolute;
  left: -99999px;
}
.qs_checkbox span {
  height: 16px;
  width: 100%;
  display: block;
  text-indent: 20px;
  background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAALklEQVQ4T2OcOXPmfwYKACPIgLS0NLKMmDVrFsOoAaNhMJoOGBioFwZkZUWoJgApdFaxjUM1YwAAAABJRU5ErkJggg==') no-repeat;
}
.qs_checkbox input:checked + span {
  background: url('data:image/png;base64,') no-repeat;
}
.qs_checkbox_label {
  position: absolute;
  top: 7px;
  left: 30px;
}
.qs_label {
  margin-bottom: 3px;
  user-select: none;
  -webkit-user-select: none;
  cursor: default;
  font: 12px sans-serif;
}
.qs_text_input {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
  padding: 0 0 0 5px;
  height: 24px;
  border: 1px inset #ffffff;
  background-color: #ffffff;
  color: #000000;
  font-size: 12px;
}
.qs_text_input:focus {
  outline: none;
  background: #ffffff;
  border: 1px inset #ffffff;
}
.qs_select {
  background: url('data:image/png;base64,') no-repeat right #f6f6f6;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  color: #000000;
  width: 100%;
  height: 24px;
  border: 1px solid #aaaaaa;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  padding: 0 5px;
  -moz-outline: none;
  font-size: 14px;
}
.qs_select option {
  font-size: 14px;
}
.qs_select::-ms-expand {
  display: none;
}
.qs_select:focus {
  outline: none;
}
.qs_number {
  height: 24px;
}
.qs_image {
  width: 100%;
}
.qs_progress {
  width: 100%;
  height: 15px;
  background-color: #cccccc;
  border: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.qs_progress_value {
  height: 100%;
  background-color: #999999;
}
.qs_textarea {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  resize: vertical;
  width: 100%;
  padding: 3px 5px;
  border: 1px inset #ffffff;
  background-color: #ffffff;
  color: #000000;
  font-size: 12px;
}
.qs_textarea:focus {
  outline: none;
  background: #ffffff;
  border: 1px inset #ffffff;
}
.qs_color {
  position: absolute;
  left: -999999px;
}
.qs_color_label {
  width: 100%;
  height: 20px;
  display: block;
  border: 1px solid #aaaaaa;
  cursor: pointer;
  padding: 0 0 0 5px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.qs_file_chooser {
  position: absolute;
  left: -999999px;
}
.qs_file_chooser_label {
  background-color: #f6f6f6;
  color: #000000;
  height: 30px;
  border: 1px solid #aaaaaa;
  font: 12px sans-serif;
  width: 100%;
  display: block;
  cursor: pointer;
  padding: 7px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}



